.application {
    height: 100%;
    width: 100% !important; 
    max-width: 1350px !important;
    
}

body {
    background-color: #f7f7f7;
}

.breadcrumbs {
    margin-top: 20px !important;
}

.container-application{
    display: flex;
    margin-top: 10px;
}

.link{
    text-decoration: none;
    color: inherit;
}

.link-breadcrumbs {
    cursor: pointer;
}

.carregando-pagina-app {
    display: block;
    color: grey;
    text-align: center;
    max-height: 379px;
    padding: 189px 0 189px 0;
}
.topbar {
  width: 100%;
  height: 50px;
  background-color: white;
  position: -webkit-sticky;
  position: sticky;
  top: 0;
  z-index: 999;
}

.topbarWrapper {
  height: 100%;
  padding: 0px 20px;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.logo {
  font-weight: bold;
  font-size: 30px;
  color: darkblue;
  cursor: pointer;
}

.topMiddle{
  display: flex;
  align-items: center;
}

.topRight{
    display: flex;
    align-items: center;
}

.topbarIconContainer {
  position: relative;
  cursor: pointer;
  margin-right: 10px;
  color: #555;
}

.topIconBadge{
    width: 15px;
    height: 15px;
    position: absolute;
    top: -5px;
    right: 0px;
    background-color: red;
    color: white;
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 10px;
}

.topAvatar{
    width: 40px;
    height: 40px;
    border-radius: 50%;
    cursor: pointer;
}
